@import '../variables.css';
@import '../global.css';

.test-page {
  padding: 20px;
  font-family: 'Montserrat', sans-serif;
  color: var(--text-color);
}

.question {
  margin-bottom: 20px;
}

.question p {
  margin-bottom: 10px;
}

.question label {
  display: block;
  margin-bottom: 5px;
}

button {
  padding: 10px 20px;
  background-color: var(--button-background-color);
  color: var(--button-text-color);
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

button:hover {
  background-color: var(--button-hover-background-color);
}

.result {
  margin-top: 20px;
  font-weight: bold;
}
